import { headers } from '@/consts'
import {CollectParams, HitParams, ToolNavParams, ToolSearchParams} from '../model/toolsModel'
import request from '../request'

/* 收藏 需登录 */
export function fetchCollect(data: CollectParams) {
	return request({
		url: `/api/v1/nav/collect`,
		method: 'post',
		data,
		headers
	})
}
/* 点击 */
export function fetchHit(data: HitParams) {
  return request({
    url: `/api/v1/nav/hit`,
    method: 'post',
    data,
    headers
  })
}
/* 获取网站详情 */
export function fetchWebsiteDetail(params: { id: string }) {
	return request({
		url: `/api/v1/nav/getWebsiteDetail`,
		method: 'get',
		params,
		headers
	})
}
/* 获取导航分类 */
export function fetchNavCategoryList() {
	return request({
		url: `/api/v1/nav/loadCategory`,
		method: 'get',
		headers
	})
}
/* 获取导航数据 */
export function fetchNavList(params: ToolNavParams) {
	return request({
		url: `/api/v1/nav/loadNav`,
		method: 'get',
		params,
		headers
	})
}
/* 查询搜索分类信息 */
export function fetchNavCategorySearch() {
	return request({
		url: `/api/v1/p/getSearchCategories`,
		method: 'get',
		headers
	})
}

/* 搜索本站内容 */
export function fetchNavSearch(params: Omit<ToolSearchParams, 'pid'>) {
	return request({
		url: `/api/v1/nav/searchNav`,
		method: 'get',
		params,
		headers
	})
}
